<template>
    <div>
<video id="video" autoplay class="fileImg" width="200" height="200"></video>   
<canvas id="canvas" width="200" height="200"></canvas>
　<button class="submitButton buttonData" @click="uploadImg">上传图片</button>

 <!-- <img :src="src" width="100px" height="100px"/> -->
  </div>
   
</template>
<script>
// import { XButton } from "vux";
export default {
    data() {
        return {
            aVideo:'',
            aCanvas:'',
            ctx:'',
            src:''
        }
    },
    mounted() {
       this.get();
    },
    methods: {
        //设置dom元素
get(){
    　this.aVideo = document.getElementById('video');
      this.aCanvas = document.getElementById('canvas');
      this.ctx = this.aCanvas.getContext('2d');
      navigator.getUserMedia = navigator.getUserMedia ||
          navigator.webkitGetUserMedia ||
          navigator.mozGetUserMedia ||
          navigator.msGetUserMedia; //获取媒体对象（这里指摄像头）
    console.log(navigator.getUserMedia);
      navigator.getUserMedia({
          video: true
      }, this.gotStream, this.noStream);
       //参数1获取用户打开权限；参数二成功打开后调用，并传一个视频流对象，参数三打开失败后调用，传错误信息
}, 


gotStream(stream) {
  try{
    video.src = window.URL.createObjectURL(stream);
  }catch(err){
    video.srcObject = stream
  }
    video.onerror = function() {
        stream.stop();
    };
    stream.onended = this.noStream;
    video.onloadedmetadata = function() {
        // alert('摄像头成功打开！');
    };
},

noStream(err) {
     alert(err);
},

async uploadImg(){
     this.ctx.drawImage(this.aVideo, 0, 0, 200, 200);    
    this.src = this.aCanvas.toDataURL('image/*')      
}
    },
}
</script>

<style scoped>
    
</style>




